<template>
  <v-app>
    <template >

      <v-overlay style="opacity:0.5;z-index:9999999" v-if="componentLoading == true" >
        <div class="text-center" >
        <v-progress-circular  color="primary"  indeterminate :size="70" :width="5"></v-progress-circular>
          <h3 style="color:black">{{ componentLoadingMessage }}</h3>
        </div>
      </v-overlay>
    </template>
    <v-navigation-drawer app :mini-variant.sync="mini" style="max-width:256px">
      <div class="border-bottom toggleButton" style="line-height:41px;">
        <v-btn @click="goMiniOrMax()" fab x-small icon>
          <v-icon>
            mdi-menu
          </v-icon>
        </v-btn>
      </div>
      <div v-for="item in treeItems" :key="item.name" class="border-bottom">
        <ul class="plenty-treeview">
          <template v-if="item.hasOwnProperty('children')">
            <TreeItem :items="item" :has-child="true"></TreeItem>
          </template>
          <template v-else>
            <TreeItem :items="item"></TreeItem>
          </template>
        </ul>
      </div>
      <template v-if="$store.state.activeCrosssellers.length > 0">
        <div
            class="border-bottom"
            v-for="(variation,index) in $store.state.activeCrosssellers"
            :key="index">
          <ul class="plenty-treeview" @click="goToVar(variation)">
            <li class="">
              <div class="d-flex">
                <div class="d-flex flex-fill" :class="{ isActive : activeVarId == variation }">
                  <i aria-hidden="true" class="v-icon notranslate tree-icon mdi mdi-link-variant theme--light" style="font-size: 16px;">
                  </i>
                  <span class="flex-fill">{{ variation }}</span>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </template>
    </v-navigation-drawer>
    <v-main style="background: rgb(241, 241, 241);">
      <router-view/>
    </v-main>
  </v-app>
</template>

<script>
import TreeItem from "./components/Tree/TreeItem";

export default {
  name: 'App',
  components:
      {
        TreeItem
      },
  data: () => (
      {
        activeVarId:-1,
        mini  :false,
      }),
  created() {
    this.getInitialData();
  },

  methods:
      {
        activeVar()
        {

          let hashSplit = window.location.hash.split('/');



          for(let i = 0 ; i < this.$store.state.activeCrosssellers.length ; i++)
          {
            if(hashSplit[hashSplit.length-1] == this.$store.state.activeCrosssellers[i])
            {
              this.activeVarId = this.$store.state.activeCrosssellers[i]
              return this.activeVarId;
            }
          }
          this.activeVarId =-1;
          return -1;
        },
        goMiniOrMax:function()
        {
          if(this.mini)
          {
            this.mini = false
          }
          else
          {
            this.mini = true
          }
        },
        setCurrentActive:function(current)
        {
          for(let i = 0; i < this.treeItems.length; i++)
          {
            this.treeItems[i].active = false
          }
          for(let i = 0; i < this.treeItems.length; i++)
          {
            if(this.treeItems[i].highlight == current.name)
            {
              this.treeItems[i].active = true
            }
          }
        },
        getInitialData()
        {
          let _self = this;

          _self.$http.get( process.env.VUE_APP_GET_DATA+'0'+process.env.VUE_APP_PLUGIN_SET_PREVIEW).then((response) => {
            if(response)
            {

              let data = response.data
              _self.buildCategoryTreeData(data.categoryTree)

              let tags = []

              for(let i = 0  ; i < data.tags.length ; i++)
              {
                data.tags[i].text  = data.tags[i].tagName
                data.tags[i].value  = data.tags[i].id

                tags.push(data.tags[i])
              }

              this.$store.commit('tags',tags);

              let manfacMap = {}
              let manufacturer = [];

              for(let i = 0 ; i < data.manufacturer.entries.length ; i++)
              {
                manfacMap[data.manufacturer.entries[i].name] = data.manufacturer.entries[i].id
                manufacturer.push(data.manufacturer.entries[i].name)
              }

              this.$store.commit('manufacturer',manufacturer);
              this.$store.commit('manufacturerMap',manfacMap);

              let mandanten = [];
              let mandantMap = {};

              for(let i = 0 ; i < data.webStoreConfig.length ; i++)
              {
                mandantMap[data.webStoreConfig[i].name] = data.webStoreConfig[i].storeIdentifier
                mandanten.push(data.webStoreConfig[i].name)
              }

              this.$store.commit('mandantMap',mandantMap);
              this.$store.commit('mandanten',mandanten);
            }
          }).catch((error) => {

          })

        },
        goToVar(varId)
        {
          if(this.activeVarId !== varId)
          {
            let _self = this;

            this.$store.commit('componentLoadingMessage','getting cross seller data');
            this.$store.commit('componentLoading',true);

            let crossSeller = [];

            _self.$http.get( process.env.VUE_APP_GET_CROSSSELLER+varId+'/'+'de'+'/'+process.env.VUE_APP_PLUGIN_SET_PREVIEW).then((response) => {
              if(response)
              {

                let selectVar = response.data.selectedVar.data;

                let image = '';

                if(selectVar.images.variation.length > 0)
                {
                  let varImages  = selectVar.images.variation[0];

                  if(varImages.urlPreview !== '')
                  {
                    image = varImages.urlPreview
                  }
                  else if(varImages.urlSecondPreview == '')
                  {
                    image = varImages.urlSecondPreview
                  }
                  else if(varImages.urlMiddle == '')
                  {
                    image = varImages.urlMiddle
                  }
                }
                else if(selectVar.images.all.length > 0)
                {
                  let allImages  = selectVar.images.all[0];

                  if(allImages.urlPreview !== '')
                  {
                    image = allImages.urlPreview
                  }
                  else if(allImages.urlSecondPreview == '')
                  {
                    image = allImages.urlSecondPreview
                  }
                  else if(allImages.urlMiddle == '')
                  {
                    image = allImages.urlMiddle
                  }
                }
                if(image == '')
                {
                  image =  "./images/noimage.png";
                }

                let name  = '';

                if(selectVar.texts.name1 !== "N / A")
                {
                  name = selectVar.texts.name1
                }
                else if(selectVar.texts.name2 !== 'N / A')
                {
                  name = selectVar.texts.name2
                }
                else if(selectVar.texts.name3 !== 'N / A')
                {
                  name = selectVar.texts.name3
                }
                let manufacturer = '';

                if(selectVar.filter.hasManufacturer !== false)
                {
                  manufacturer = selectVar.item.manufacturer.name
                }
                _self.$store.commit('selectedVariation', {
                  attributes:selectVar.attributes,
                  itemId:selectVar.variation.itemId,
                  varId:selectVar.variation.id,
                  itemName:name,
                  manufacturer:manufacturer,
                  image:image,
                  externalId:selectVar.variation.externalId,
                  varNumber:selectVar.variation.number,
                  client:selectVar.ids.clients
                })

                if(response.data && response.data.vars.length > 0 )
                {
                  for(let i = 0 ; i < response.data.vars.length ; i++)
                  {
                    let currentItemData = response.data.vars[i].data;

                    let image = '';

                    if(currentItemData.images.variation.length > 0)
                    {
                      let varImages  = currentItemData.images.variation[0];

                      if(varImages.urlPreview !== '')
                      {
                        image = varImages.urlPreview
                      }
                      else if(varImages.urlSecondPreview == '')
                      {
                        image = varImages.urlSecondPreview
                      }
                      else if(varImages.urlMiddle == '')
                      {
                        image = varImages.urlMiddle
                      }
                    }
                    else if(currentItemData.images.all.length > 0)
                    {
                      let allImages  = currentItemData.images.all[0];

                      if(allImages.urlPreview !== '')
                      {
                        image = allImages.urlPreview
                      }
                      else if(allImages.urlSecondPreview == '')
                      {
                        image = allImages.urlSecondPreview
                      }
                      else if(allImages.urlMiddle == '')
                      {
                        image = allImages.urlMiddle
                      }
                    }
                    if(image == '')
                    {
                      image = "./images/noimage.png"
                    }

                    let name  = '';

                    if(currentItemData.texts.name1 !== "N / A")
                    {
                      name = currentItemData.texts.name1
                    }
                    else if(currentItemData.texts.name2 !== 'N / A')
                    {
                      name = currentItemData.texts.name2
                    }
                    else if(currentItemData.texts.name3 !== 'N / A')
                    {
                      name = currentItemData.texts.name3
                    }

                    let manufacturer = '';

                    if(currentItemData.filter.hasManufacturer !== false)
                    {
                      manufacturer = currentItemData.item.manufacturer.name
                    }

                    let object = {
                      itemId:currentItemData.variation.itemId,
                      varId:response.data.vars[i].id,
                      itemName:name,
                      manufacturer:manufacturer,
                      image:image,
                      position:response.data.vars[i].position,
                      externalId:currentItemData.variation.externalId,
                      varNumber:currentItemData.variation.number,
                      client:currentItemData.ids.clients,
                      attributes:currentItemData.attributes
                    }
                    crossSeller.push(object)
                  }

                }
              }

              _self.$store.commit('crossSeller',crossSeller)
              _self.$store.commit('componentLoading',false);

              _self.$router.push({path: '/CrossSeller/'+varId})

            }).catch((error) => {
              _self.$store.commit('componentLoading',false);
            })
          }
        },
        buildCategoryTreeData(data)
        {
          let categories = [];

          for(let i = 0 ; i < data.length ; i++)
          {
            let category = {}
            if(data[i].hasOwnProperty("details") && data[i].details[0].hasOwnProperty("name"))
            {
              category.name = data[i].details[0].name
            }
            if(data[i].hasOwnProperty("id"))
            {
              category.id =  data[i].id;
            }
            if(data[i].hasOwnProperty("parentCategoryId"))
            {
              category.parentCategoryId =  data[i].parentCategoryId;
            }

            if(data[i].hasOwnProperty("childCount") && data[i].childCount > 0)
            {
              category.children = this.buildChildTree(data[i])
            }
            categories.push(category)
          }

          this.$store.commit('categoryTree',categories);
        },
        buildChildTree(data)
        {
          let children = [];

          for(let i = 0 ; i < data.childCount ; i++)
          {
            if(data.children[i])
            {

              let child = {
                children: []
              }

              if(data.children[i].hasOwnProperty("id"))
              {
                child.id =  data.children[i].id;
              }
              if(data.children[i].hasOwnProperty("parentCategoryId"))
              {
                child.parentCategoryId =  data.children[i].parentCategoryId;
              }

              if(data.children[i].hasOwnProperty("details") && data.children[i].details.length > 0 && data.children[i].details[0].name)
              {
                child.name = data.children[i].details[0].name
              }
              if(data.children[i].childCount > 0)
              {
                child.children = this.buildChildTree(data.children[i])
              }

              children.push(child)
            }
          }
          return children;
        },

      },
  watch: {
    $route() {
      this.setCurrentActive(this.$router.currentRoute)
      this.activeVar()
    }
  },
  computed:
      {
        componentLoadingMessage:function () {
          return this.$store.state.componentLoadingMessage;
        },
        componentLoading:function () {
          return this.$store.state.componentLoading;
        },
        treeItems:function ()
        {
          let items =
              [
                {
                  name: "Dashboard",
                  icon:'chart-bar',
                  key:'dashboard',
                  url:'/',
                  active:true
                },
              ];
          return items;
        }
      }
};
</script>

